.fade-enter {
  opacity: 0;
  transition: all 1s;
}

.fade-enter-active {
  opacity: 1;

}

.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: 800ms;
}



.object-fade {
  animation: fadeIn 1s ease-in-out;
}



.loading-icon {
  height: 100px;
  width: auto;
}



